.halls {
    margin: 95px auto 21px;
    &__title {
        margin-bottom: 47px; }
    &__halls {
        @include flex-center;
        flex-wrap: wrap;
        margin: 0 -15px; } }
.hall {
    flex: 0 0 50%;
    width: 50%;
    padding: 0 15px 46px;
    text-align: center;
    @include sm {
        flex: 0 0 100%;
        width: 100%; }
    &__image {
        display: block;
        @include cover;
        padding-bottom: 314px;
        margin-bottom: 26px; }
    &__title {
        font-size: 20px;
        font-weight: 700;
        line-height: 1.05;
        text-transform: uppercase;
        margin-bottom: 8px; }
    &__props {
        font-size: 18px;
        line-height: 1.17;
        margin-bottom: 9px;
        b {
            margin-left: 5px; } }
    &__descr {
        max-width: 480px;
        margin: 0 auto 7px; }
    &__link {
        font-size: 16px;
        font-weight: 700;
        color: $darkGreen;
        border-bottom: 1px solid $darkGreen; } }
